@import '_resets';

// Sass variables
// Sass global variables must come BEFORE partials that use them and declared on the 'main.scss' file
$primary-color                   : #f2f2f2;
$primary-shade                   : #ffffff2e;
$secondary-color                 : #555;
$secondary-shade                 : #565;
$accent-color                    : #1b4470;
$body-font-family                : 'Lato', sans-serif;;
$heading-font-family             : 'Lato', sans-serif;;
$heading-default-bottom-margin   : 2rem;
$gallery-padding                 : 1.5rem;

// Sass partials
// Add necessary partials from here onwards
@import
  '_mixins',
  '_universal',
  '_utility',
  '_padding',
  '_containers',
  '_buttons',
  '_nav',
  '_footer',
  '_blocks',
  '_milestones',
  '_program-details',
  '_highlights',
  '_application',
  '_cta',
  '_media-queries';

